.profile__about {
  width: 336px;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 1279px) {
  .profile__about {
    width: calc((100vw - 320px) / (1279 - 320) * (336 - 282) + 282px);
    line-height: calc((100vw - 320px) / (1279 - 320) * (22 - 17) + 17px);
    font-size: calc((100vw - 320px) / (1279 - 320) * (18 - 14) + 14px);
  }
}

@media screen and (max-width: 1020px) {
  .profile__about {
    text-align: center;
  }
}
